.UserDetails {
  &__searchBar-wrapper {
    text-align: center;
  }

  &__searchBar {
    width: 250px;
    height: 40px;
    font-size: 16px;
    font-weight: 700;
    border: 2px solid #4d457b;
    color: #4d457b;
  }
  &__info {
    display: flex;
    gap: 20px;
    margin-bottom: 5px;
  }
  &__user-info {
    display: flex;
    gap: 3px;
    flex-direction: column;
    font-weight: 700;
  }
  &__bio {
    text-align: center;
    margin-bottom: 20px;
    font-weight: 700;
    text-decoration: underline;
  }
  &__avatar {
    border-radius: 8px;
    width: 120px;
    height: 120px;
  }
  &__post {
    padding: 10px 0 15px;
  }
  &__comments {
    text-align: right;
  }
  &__list {
    list-style: none;
  }
  &__list-item {
    margin: 5px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border-radius: 10px;
    font-style: italic;
    font-size: 1rem;
    font-weight: 300;
    transition: background-color 0.12s;
    background: linear-gradient(
                    to right, rgba(255, 255, 255, 1) 0%,
                    rgba(246, 246, 246, 1) 47%,
                    rgba(250, 250, 253, 1) 100%
    );
    &:hover {
      color: #fff;
      background: #4d457b;
    }
  }
  &__remove-button {
    min-width: 20px;
    color: inherit;
    background-color: transparent;
    transition: color 0.12s;
    margin-right: 10px;
    &:hover {
      color: #e9e6f2;
    }
    &:hover + p {
      transition: color 0.12s;
    }
  }
  &__form-wrapper {
    background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='10' ry='10' stroke='%234D457BFF' stroke-width='4' stroke-dasharray='6%2c 14' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
    border-radius: 10px;
  }
}
